<template>        
    <div id="1" class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <h3>4. 要保人職業 ?</h3>
    </div>
    <br>
    <div class="card" style="width: 60%; margin: 0 auto; padding: 30px 30px;">
        <div class="card-body">
            <div>
                <input type="text" v-model.trim="career" class="form-control" placeholder="輸入要保人職業">
                <br>
                <button @click="press" class="btn btn-outline-success" type="button">
                    <b>確認</b>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data (){
        return {
            career: ''
        }
    },
    methods: {
        press: function(){
            if(this.career.length == 0){
                alert("請填寫要保人職業");
            }
            else{
                if(this.$route.params.samePeople == 1){
                    this.$router.push({ 
                        name: '6',
                        params:{
                            name: this.$route.params.name,
                            type: this.$route.params.type,
                            samePeople: this.$route.params.samePeople,
                            insurerCareer: this.career,
                            insuredCareer: this.career
                        }
                    });
                }
                else{
                    this.$router.push({ 
                        name: '5',
                        params:{
                            name: this.$route.params.name,
                            type: this.$route.params.type,
                            samePeople: this.$route.params.samePeople,
                            insurerCareer: this.career
                        }
                    });
                }
            }
        }
    }
};
</script>